
body {
    background-image: url("../../public/img\ territorios/HomeFinal.png");
    background-size: cover;
    height: 100%;
    background-repeat: no-repeat;
    font-family: "Quicksand";
    font-size: x-large;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  a {
    text-decoration: none;
  }

  #pdfText {
    color: rosybrown;
    text-decoration: none;
  }
  #pdfText:visited {
    color:rosybrown;
    text-decoration: none;
  }

  #Texto {
    font-family: "Quicksand";
    font-size: large;
  }
  
  footer {
    padding: 1px;
    background-color: rgb(218, 223, 231);
  }
  
  #Horarios {
    font-size: larger;
    color: #000;
    font-family: "Alata";
    text-shadow: 2px 0 #fff, -2px 0 #fff, 0 2px #fff, 0 -2px #fff,
    1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;
  }
  
  .sidebar {
    font-family: "Alata";
    background-color: #beb49e; /* Color de fondo del sidebar */
    height: 100vh;
    width: 230px; /* Ancho del sidebar */
    position: fixed;
    top: 0;
    left: 0;
    overflow-x: hidden;
    z-index: 999;
    padding-top: 20px;
    display: block; /* Por defecto, el sidebar se muestra */
    transition: transform 0.3s ease; /* Transici칩n para suavizar el movimiento */
    transform: translateX(0); /* Por defecto, el sidebar se muestra */
  }
  
  
  .visibleSideBar {
    transform: translateX(0) !important; /* Muestra el sidebar */
  }
  
  .hiddenSideBar {
    transform: translateX(-100%) !important; /* Oculta el sidebar movi칠ndolo fuera de la vista */
  }
  
  .visibleContent {
    margin: 0 0 0 200px;
  }
  
  .hiddenContent {
    margin: 0 auto;
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar ul li {
    padding: 8px 16px;
  }
  
  .sidebar ul li a {
    color: white;
    text-decoration: none;
  }
  
  .sidebar ul li a:hover {
    background-color: #af987ece; /* Cambia el color de fondo al pasar el mouse */
  }
  
  .dropdown {
  position: relative;
  display: inline-block;
}

.dropbtn {
  background-color: cornflowerblue;
  color: white;
  padding: 12px;
  font-size: 22px;
  border: none;
  cursor: pointer;
}

.dropdown-content {
  position: absolute;
  background-color: #f9f9f9e7;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgb(124, 148, 192);
  z-index: 1;
}

.dropdown-content a {
  color: rgb(9, 62, 160);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {background-color: #b9b9d48e}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color:  rgb(9, 62, 160);
}

  
  .toggle-btn {
    position: fixed;
    top: 80px;
    right: 60px;
    z-index: 999;
    border: none;
    background: none;
    padding: 0;
    cursor: pointer;
  }
  
  .toggle-btn img {
    width: 60px;
    height: auto;
  }
  
  
  .welcome {
    background: #62815f;
  }
  
    a:visited {
        color: rgb(15, 14, 14); /* Cambia el color del enlace visitado a p칰rpura */
    }

button{
    &:disabled{
        background-color: rgba(107, 110, 110, 0.267);
        cursor: not-allowed;
    }
}

@media (max-width: 768px) {
  .sidebar {
    width: 100%; /* El sidebar ocupa el ancho completo en pantallas peque침as */
    font-size: x-small;
    height: auto; /* Permitir que el sidebar ajuste su altura autom치ticamente */
    display: none; /* Ocultar el sidebar por defecto en pantallas peque침as */
  }

  .visibleContent {
    margin: 0 !important; /* Ajustar el margen para el contenido visible */
  }

  .toggle-btn {
    right: 10px !important; /* Mover el bot칩n de toggle a la derecha */
    top: 5px !important;
  }

  .toggle-btn img {
    width: 50px !important;
  }

  .dropdown-content {
    font-size: medium !important; /* Ajustar el tama침o de fuente para pantallas peque침as */
  }

  #imgUpl {
    max-width: 300px;
  }

}


@media (max-width: 480px) {

  .sidebar {
    display: block !important; /* Mostrar el sidebar en pantallas peque침as si es necesario */
    width: 100% !important;
    height: 100% !important;
    font-size: xx-large !important;
  }

  .toggle-btn {
    display: block; /* Asegurarse de que el bot칩n de toggle est칠 visible */
    right: 10px !important; /* Mover el bot칩n de toggle a la derecha */
    top: 5px !important;
  }

  .toggle-btn img {
    width: 50px !important;
  }

  .visibleContent {
    margin: 0 !important; /* Ajustar el margen para el contenido visible */
  }

  #dropdown-content {
    font-size: small !important; /* Ajustar el tama침o de fuente para pantallas muy peque침as */
  }

  #imgUpl {
    max-width: 300px;
  }

}